<template>
    <div class="wrapper">
        <div class="search">
            <div class="search__back iconfont"
            @click="handbackclick"> &#xe6f2;</div>
            <div class="search__content">
                <span class="search__content__icon iconfont">&#xe62d;</span>
                <input class="search__content__input"
                placeholder="请输入商品名称"
                @change="handsearchchange"
                v-model="keyword"
              />
            </div>
        </div>
    <router-link :to="`/shop/${item._id}`" 
    v-for="item in searchList" 
    :key="item.id">
    <shopinfo :item="item"/>
  </router-link>
    </div>
</template>
<script>
import { get } from '../../utils/request'
 import {ref} from 'vue'
 import { useRoute, useRouter} from 'vue-router'
 import shopinfo from '../../components/shopinfo'
 const usebackroutereffect = ()=>{
        const router = useRouter()  
        const handbackclick =()=>{
          router.back()
    }
    return handbackclick
}
const usesearchlisteffect =()=>{
  const searchList = ref([])
   const getsearchlist = async(keyword)=>{
    const result = await get('/api/shop/search',{keyword})
    if(result?.errno ===0 && result?.data?.length){
        searchList.value = result.data;
   
  }
   }
   return{getsearchlist,searchList}
}
  export default {
      name:'searchlist',
      components:{shopinfo},
      setup(){
        const route = useRoute();
        console.log(route.keyword)
        const keyword = ref(route.query.keyword || ' ')
        const handsearchchange = ()=>{
            getsearchlist(keyword.value);
        }
        const handbackclick =usebackroutereffect()
        // 获取搜索列表
        const {getsearchlist,searchList} =usesearchlisteffect();
        getsearchlist(keyword.value);
        return{handbackclick,keyword,handsearchchange,searchList}
      }
  }
</script>
<style lang="scss" scoped>

.wrapper{
    padding: 0 .18rem;
    a{
        text-decoration: none;
     }
}
.search{
        display: flex;
        margin: .14rem 0 .04rem 0;
        line-height:  .32rem;
      &__back{
        width: .3rem;
        font-size: .24rem;
        color:#b6b6b6;
      }
      &__content{
        display: flex;
        flex: 1;
        background: #f5f5f5;
        border-radius: .16rem;
        &__icon{
           width: .44rem;
          text-align: center;
           color: #b7b7b7;
        }
        &__input{
            display: block;
            width: 100%;
            padding-right: .2rem;
            border: none;
            outline: none;
            background: none;
            height: .32rem;
            font-size: .14rem;
            color: #333;
            &::placeholder{
                color: #333;
            }
        }
      }
    }
</style>
